import { BOT_CONFIG } from './bot.js';

export const TG = window.Telegram?.WebApp;

export const initTelegram = () => {
    console.log('Initializing Telegram...');
    
    if (!TG) {
        console.error('Telegram WebApp is not available');
        return;
    }

    try {
        // Проверяем инициализацию с правильным ботом
        if (TG.initDataUnsafe?.user?.username !== BOT_CONFIG.BOT_USERNAME) {
            console.error('Invalid bot initialization');
            return;
        }

        // Инициализация
        TG.ready();
        TG.expand();

        // Настраиваем тему
        TG.setHeaderColor('#000000');
        TG.setBackgroundColor('#000000');
        
        console.log('Telegram initialization complete');
    } catch (error) {
        console.error('Error in Telegram initialization:', error);
    }
}; 